@font-face {
  font-family: "Mont-SemiBold";
  src: url("../fonts/Mont/Mont-SemiBold.eot");
  src: local("Mont SemiBold"),
    local("Mont-SemiBold"),
    url("../fonts/Mont/Mont-SemiBold.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Mont/Mont-SemiBold.woff") format("woff"),
    url("../fonts/Mont/Mont-SemiBold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Mont-Regular";
  src: url("../fonts/Mont/Mont-Regular.eot");
  src: local("../fonts/Mont/Mont Regular"),
    local("Mont-Regular"),
    url("../fonts/Mont/Mont-Regular.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Mont/Mont-Regular.woff") format("woff"),
    url("../fonts/Mont/Mont-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon/fonts/icomoon.eot?lxursf");
  src: url("../fonts/icomoon/fonts/icomoon.eot?lxursf#iefix") format("embedded-opentype"),
    url("../fonts/icomoon/fonts/icomoon.ttf?lxursf") format("truetype"),
    url("../fonts/icomoon/fonts/icomoon.woff?lxursf") format("woff"),
    url("../fonts/icomoon/fonts/icomoon.svg?lxursf#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

/* stylelint-disable */
[class^="icon-"],
[class*=" icon-"] {
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-favorite:before {
  content: "\e900";
}
/* stylelint-enable */
